
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}








































.page-login form {
  width: 400px;
  margin: 0 auto;
}
.form-group {
  margin-bottom: 15px;
  position: relative;
}
.form-group .form-control,
.form-group button {
  border: 1px solid #e5e5e5;
  border-radius: 3px;
  outline: none;
  height: 30px;
  width: 100%;
  padding: 5px;
}
.form-group button {
  outline: none;
  border: 1px solid #41b883;
  background-color: #41b883;
  width: auto;
  color: #fff;
}









































































